import Vue from 'vue';

import tml from './Confirm.vue';

var _ComfirmPopover = Vue.extend(tml);
var instance = undefined;

var ComfirmPopover = function(options){
	options = options || {};
    //生成组件
    instance = new _ComfirmPopover({
        'propsData': options
    });
    //组件需要挂载在dom元素上
    instance.vm = instance.$mount();

    return instance.vm;
};

export default ComfirmPopover;





