<template>
  <div>
    <blockquote class="explain-txt"></blockquote>
    <button class='btn' @click='handlePopover'>按钮</button>
  </div>
</template>
<script type="text/javascript">
import ConfirmPopover from '@/components/popover/Confirm.js';

export default {
  'name': 'modulePopover',
  'methods': {
    handlePopover() {
      new ConfirmPopover({
        'arrow': 'bottom-center',
        'target': event.target,
        'callBack': function() {
          alert("测试回调");
        }
      });
    }
  }
};

</script>



