/**
 * [提示弹窗]
 *
 * @param  {[Object]} target [点击目标元素],必填
 *
 * @param  {[String]} arrow [箭头方向],默认为"left-center"，有12个方向
 * 例:"left-top" "left-center" "left-bottom" "bottom-left" "bottom-center" "bottom-right" "right-top" "right-center" "right-bottom" "top-left" "top-center" top-right
 *
 * @param  {[String]} content [提示内容],默认为"确定删除？"
 *
 * @param  {[Boolean]} popNotAutoHide [是否点击其他地方会自动隐藏popover],默认为true
 *
 * @param  {[Function]} callBack [回调函数],必填
 *
 */
<template>
    <div :class="['ui-popover ui-popover--confirm',arrow]">
        <div class="ui-popover-inner clearfix">
            <div class="inner__header clearfix">
                <div class="pull-left text-center"
                	style="width: 80px;line-height: 28px;font-size: 14px;">{{ content }}</div>
                <div class="pull-right">
                	<a href="javascript:;"
                		class="zent-btn zent-btn-primary zent-btn-small"
                		@click="handleSubmit">确定</a>
                	<a href="javascript:;"
                		class="zent-btn zent-btn-small"
                		@click="destroyConfirm">取消</a>
                </div>
            </div>
        </div>
        <div class="arrow"></div>
    </div>
</template>
<script type="text/javascript">
	export default {
		'data':function(){
			return {
				'class':''
			};
		},
		'props':{
			'target':Object,//点击目标元素
			'arrow':{//箭头方向
				'type':String,
				'default':'left-center'
			},
			'content':{//内容
				'type':String,
				'default':'确定删除？'
			},
			'popNotAutoHide':{	//是否点击其他地方会自动隐藏popover
				'type':Boolean,
				'default':true
			},
			'callBack':Function //回调函数
		},
		'mounted':function(){
			this.class = this.getClass();

			if($(this.class).length>0){
                this.destroyConfirm();
            }

			$(this.$el).appendTo('body').hide();

            var popoverXY = this.getPosition();

            $(this.$el).css({
                'top':popoverXY.y+'px',
                'left':popoverXY.x+'px'
            }).show();

			if(this.popNotAutoHide){
				this.listenBodyClick();
			}
		},
		'methods':{
			/**
	    	 * [getClass 获取class]
	    	 * @return {[type]} [description]
	    	 */
			'getClass':function(){
				var _class = this.$el.className.split(' ');

				$.each(_class,function(index,item){
					_class[index] = '.'+item;
				});
				_class = _class.join('');
				return _class;
			},
			/**
			 * [getPosition 设置popover的坐标]
			 * @return {[type]} [description]
			 */
			'getPosition':function () {
				// 目标坐标以宽高信息
				var $targetInfo = this.getTargetInfo();
				// popover宽高信息
				var $popoverInfo = this.getPopoverInfo();
				// 方向
				var _arrow = this.arrow.split('-');

				if(_arrow[0]==='left'){
					$targetInfo.x -= $popoverInfo.w;
				}else if(_arrow[0]==='right'){
					$targetInfo.x += $targetInfo.w;
				}else if(_arrow[0]==='top'){
					$targetInfo.y += $targetInfo.h;
				}else if(_arrow[0]==='bottom'){
					$targetInfo.y -= $popoverInfo.h;
				}

				if(_arrow[0]==='left' || _arrow[0]==='right'){
					if ( _arrow[1] === 'center' ) {
						$targetInfo.y -= ($popoverInfo.h - $targetInfo.npbH) / 2;
					}else if(_arrow[1] === 'top'){
						$targetInfo.y -= $targetInfo.h / 2;
					}else if(_arrow[1] === 'bottom'){
						$targetInfo.y = $targetInfo.y - 1 - $popoverInfo.h/2 - $targetInfo.h/4;
					}
				}else if(_arrow[0]==='top' || _arrow[0]==='bottom'){
					if ( _arrow[1] === 'center' ) {
						$targetInfo.x = $targetInfo.x - ($popoverInfo.w - $targetInfo.w)/2 + ($targetInfo.w - $targetInfo.npbW)/2;
					}else if(_arrow[1] === 'right'){
						$targetInfo.x -= ($popoverInfo.w - $targetInfo.w);
					}
				}

				$targetInfo.x = Math.round( $targetInfo.x );
				$targetInfo.y = Math.round( $targetInfo.y );

				return $targetInfo;
			},
			/**
			* [getTargetInfo 目标坐标以宽高信息]
			* @return {[type]} [description]
			*/
			'getTargetInfo':function(){
				return {
					'x':this.target.offset().left,
					'y':this.target.offset().top,
					'h':this.target.outerHeight(),
					'w':this.target.outerWidth(),
					'npbH':this.target.height(),
					'npbW':this.target.width()
				};
			},
			/**
			 * [getPopoverInfo popover宽高信息]
			 * @return {[type]} [description]
			 */
			'getPopoverInfo':function(){
				return {
					'h':$(this.class).outerHeight(),
					'w':$(this.class).outerWidth()
				};
			},
			/**
			 * [handleSubmit 确定事件]
			 * @return {[type]} [description]
			 */
			'handleSubmit':function(){
				this.callBack();
				this.$destroy();
			},
			/**
			 * [listenBodyClick 鼠标点击外部的时候，关闭当前弹出框]
			 * @return {[type]} [description]
			 */
			'listenBodyClick':function(){
				//只绑定指定命名空间(_uid.popover.ui（注意，生成之后是倒过来的名称）)的事件
				$('body').on('click.ui.popover.'+this._uid,function(e){
					this.autoClose(e);
				}.bind(this));
			},
			'autoClose':function(e){
				var _con = $(this.class);// 设置目标区域

				if(!_con.is(e.target) && _con.has(e.target).length === 0){//目标不存在
					this.$destroy();
				}
			},
			/**
			 * [destroyConfirm 销毁操作]
			 * @return {[type]} [description]
			 */
			'destroyConfirm':function(){
				//只移除指定命名空间的事件绑定
				$('body').off('click.ui.popover.'+this._uid);
				$(this.class).remove();
			}
		},
		'beforeDestroy':function(){
			this.destroyConfirm();
		}
	};
</script>


